.content-list-tasks {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    ul {
        li {
            list-style: none;
            border-bottom: 2px solid #666;
            padding: 10px;
            a {
                color: #777;
                text-decoration: none;
                &:hover {
                    background: #eee;
                }
            }
        }
    }
}